import Bmob from "hydrogen-js-sdk";
import print from "@/utils/print.js"
import common from "@/utils/common.js"

import loading from "@/components/Loading/index.vue"
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'

let that;
let id;
export default {
	components: {
		loading,
		uniNavBar
	},
	data() {
		return {
			user: uni.getStorageSync("user"),
			identity: uni.getStorageSync("identity"),
			othercurrent: '',
			bills: [],
			loading: true,
			products: null,
			detail: null,
		}
	},
	onLoad(options) {




		console.log(options)
		that = this;
		id = options.id;
		if (that.user.rights && that.user.rights.othercurrent) {
			that.othercurrent = that.user.rights.othercurrent
		}
		that.getdetail(id);
	},

	//分享
	onShareAppMessage: function(res) {
		if (res.from === 'button') {
			// 来自页面内转发按钮
			console.log(res.target)
		}
		return {
			title: '库存表-操作单详情',
			path: '/pages/report/EnteringHistory/detail/detail?id=' + id
		}
	},

	methods: {

		gotoexpressDet() {
			if (that.user.is_vip) {
				uni.navigateTo({
					url: "../expressDet/expressDet?number=" + that.detail.expressNum
				})
			} else {
				uni.showToast({
					title: "您还不是会员，无法使用",
					icon: 'none'
				})
			}
		},

		//预览图片
		priview(url) {
			uni.previewImage({
				current: url,
				urls: that.detail.Images,
			});
		},

		//点击显示操作菜单
		show_options() {
			let options = []

			if (that.detail.type == 1 || that.detail.type == -1) {
				if (that.detail.type == 1 && that.detail.extra_type == 1) {
					options = ['撤销', '打印', "采购入库"]
				} else if (that.detail.type == -1 && that.detail.extra_type == 1) {
					options = ['撤销', '打印', '销售出库']
				} else {
					options = ['撤销', '打印']
				}

			} else {
				options = ['打印']
			}
			uni.showActionSheet({
				itemList: options,
				success: function(res) {
					if (res.tapIndex == 0) {
						that.revoke()
						uni.setStorageSync("is_option", true)
					} else if (res.tapIndex == 1) {
						print.print_operations(that.detail, that.products)
					} else if (res.tapIndex == 2) {
						that.confrimOrder()
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},

		getdetail: function(id) {
			const query = Bmob.Query('order_opreations');
			query.include("opreater", "custom", "producer", "stock","shop","account");
			query.get(id).then(res => {
				console.log(res);
				that.detail = res;
				for(let item of res.detail){
					if(item.packingUnit && item.packingUnit !='undefined'){
						item.packingUnit = item.packingUnit
					}else{
						item.packingUnit = ''
					}
				}
				that.products = res.detail;
				that.bills = res.bills;
				that.loading = false;
			}).catch(err => {
				console.log(err)
			})
		},

		//数据撤销点击
		revoke: function() {
			wx.showModal({
				title: '提示',
				content: '数据撤销后不可恢复，请谨慎撤销！',
				success(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '撤销中...'
						})
						const query = Bmob.Query('order_opreations');
						query.destroy(that.detail.objectId).then(res => {
							const query = Bmob.Query('Bills');
							query.containedIn("objectId", that.bills);
							query.find().then(todos => {

								todos.destroyAll().then(res => {
									// 成功批量修改
									if (that.detail.status) {
										for (var i = 0; i < that.products.length; i++) {
											that.delete_bill(i);
										}
									} else {
										uni.hideLoading();
										uni.navigateBack({
											delta: 1
										})
										setTimeout(function() {
											uni.showToast({
												title: '撤销成功'
											})
										}, 1000);
									}

								}).catch(err => {
									console.log(err)
								});
							})
						}).catch(err => {
							console.log(err)
						})
					}
				}
			})
		},

		//审核订单
		confrimOrder() {
			wx.showModal({
				title: '提示',
				content: '是否审核该笔操作单！',
				success(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '审核中，请勿退出该页面...'
						})
						
						if(that.detail.status){
							uni.showToast({
								title:"已经审核过了...",
								icon:'none'
							})
							uni.hideLoading()
							
							return false
						}
						
						const query = Bmob.Query('order_opreations');
						query.set('id', id) //需要修改的objectId
						query.set('status', true)
						query.save().then(res => {
							//console.log(res)
							let count = 0
							if (that.detail.type == 1) {
								for (let item of that.products) {
									that.addOrReduceGoodReserve(item, count);
									count += 1;
								}
							} else if (that.detail.type == -1) {
								for (let item of that.products) {
									that.ReduceGoodReserve(item, count);
									count += 1;
								}
							}

						}).catch(err => {
							console.log(err)
						})
					}
				}
			})
		},

		//采购单确定审核之后改变产品库存
		addOrReduceGoodReserve(product, count) {
			const query1 = Bmob.Query('Goods');
			query1.get(product.goodsId.objectId).then(res => {
				console.log(res)
				let thisGood = res;
				if (product.goodsId.selected_model) {
					let num = 0;
					for (let model of product.goodsId.selected_model) {
						for (let item of res.models) {
							if (item.id == model.id) {
								item.reserve = Number(item.reserve) + Number(model.num)
								//console.log(item.reserve)
								num += Number(model.num)
							}
						}
					}
					//console.log(res.models)
					res.set('models', res.models)
					res.set('reserve', res.reserve + num);
				} else {
					res.set('reserve', res.reserve + product.num);
				}

				res.save().then(res => {
					if (thisGood.header) {
						const query1 = Bmob.Query("Goods");
						query1.equalTo("header", "==", thisGood.header.objectId);
						query1.equalTo("order", "==", 1);
						query1.statTo("sum", "reserve");
						query1.find().then(res => {
							console.log("dasds", res)
							let now_reserve = res[0]._sumReserve
							const query = Bmob.Query('Goods');
							query.set('reserve', now_reserve)
							
							if(thisGood.order=="" || thisGood.order==null || thisGood.order==undefined){									
								if(thisGood.max_num >=0){
									if(num >= thisGood.max_num){
										query.set('stocktype', 2)
									}else if(num <= thisGood.warning_num){
										query.set('stocktype', 0)
									}else{
										query.set('stocktype', 1)
									}
								}else{
									query.set('stocktype', 1)
								}
							}
							
							query.set('id', thisGood.header.objectId)
							query.save().then(res => {
								if (count == (that.products.length - 1)) {
									const query = Bmob.Query('Bills');
									query.containedIn("objectId", that.bills);
									query.find().then(todos => {
										todos.set('status', true);
										todos.saveAll().then(res => {
											uni.hideLoading();
											uni.navigateBack({
												delta: 1
											})
											setTimeout(function() {
												uni.showToast({
													title: '审核成功'
												})
											}, 1000);
											//console.log(res, 'ok')
										}).catch(err => {
											console.log(err)
										});
									})
								}
							})
						})
					}else{
						if (count == (that.products.length - 1)) {
							const query = Bmob.Query('Bills');
							query.containedIn("objectId", that.bills);
							query.find().then(todos => {
								todos.set('status', true);
								todos.saveAll().then(res => {
									uni.hideLoading();
									uni.navigateBack({
										delta: 1
									})
									setTimeout(function() {
										uni.showToast({
											title: '审核成功'
										})
									}, 1000);
									//console.log(res, 'ok')
								}).catch(err => {
									console.log(err)
								});
							})
						}
					}
					
				})
			})
		},

		//销售单确认审核之后减少库存
		ReduceGoodReserve(product, count) {
			const query1 = Bmob.Query('Goods');
			let now_reserve = 0;
			query1.get(product.goodsId.objectId).then(res => {
				console.log(res)
				let thisGood = res;
				if (product.goodsId.selected_model) {
					let num = 0;
					for (let model of product.goodsId.selected_model) {
						for (let item of res.models) {
							if (item.id == model.id) {
								item.reserve = Number(item.reserve) - Number(model.num)
								//console.log(item.reserve)
								num += Number(model.num)
							}
						}
					}
					//console.log(res.models)
					res.set('models', res.models)
					res.set('reserve', res.reserve - num);
					now_reserve = res.reserve - num
				} else {
					res.set('reserve', res.reserve - product.num);
					now_reserve = res.reserve - product.num
				}

				console.log(product)
				res.save().then(res => {
					if (thisGood.header) {
						const query1 = Bmob.Query("Goods");
						query1.equalTo("header", "==", thisGood.header.objectId);
						query1.equalTo("order", "==", 1);
						query1.statTo("sum", "reserve");
						query1.find().then(res => {
							console.log("dasds", res)
							let now_reserve = res[0]._sumReserve
							const query = Bmob.Query('Goods');
							query.set('reserve', now_reserve)
							if(thisGood.order=="" || thisGood.order==null || thisGood.order==undefined){
								if(thisGood.warning_num >=0){
									if(num >= thisGood.max_num){
										query.set('stocktype', 2)
									}else if(num <= thisGood.warning_num){
										query.set('stocktype', 0)
									}else{
										query.set('stocktype', 1)
									}
								}else{
									query.set('stocktype', 1)
								}
							}
							query.set('id', thisGood.header.objectId)
							query.save().then(res => {
								if (product.warning_num >= now_reserve && product.warning_num>=0) {
									common.log(product.goodsName + "销售了" + product.num + "件，已经低于预警数量" + product.warning_num, -2, product.goodsId
										.objectId);
								}

								if (count == (that.products.length - 1)) {
									const query = Bmob.Query('Bills');
									query.containedIn("objectId", that.bills);
									query.find().then(todos => {
										todos.set('status', true);
										todos.saveAll().then(res => {
											uni.hideLoading();
											uni.navigateBack({
												delta: 1
											})
											setTimeout(function() {
												uni.showToast({
													title: '审核成功'
												})
											}, 1000);
											//console.log(res, 'ok')
										}).catch(err => {
											console.log(err)
										});
									})
								}
							})
						})
					} else {
						if (product.warning_num >= now_reserve) {
							common.log(product.goodsName + "销售了" + product.num + "件，已经低于预警数量" + product.warning_num, -2, product.goodsId
								.objectId);
						}

						if (count == (that.products.length - 1)) {
							const query = Bmob.Query('Bills');
							query.containedIn("objectId", that.bills);
							query.find().then(todos => {
								todos.set('status', true);
								todos.saveAll().then(res => {
									uni.hideLoading();
									uni.navigateBack({
										delta: 1
									})
									setTimeout(function() {
										uni.showToast({
											title: '审核成功'
										})
									}, 1000);
									//console.log(res, 'ok')
								}).catch(err => {
									console.log(err)
								});
							})
						}
					}
				})
			})
		},

		delete_bill: function(i) {
			let product = that.products[i];
			let bill = that.bills[i]

			const query1 = Bmob.Query('Goods');
			query1.get(product.goodsId.objectId).then(res => {
				console.log(res)
				let thisGood = res;
				query1.set('id', product.goodsId.objectId);
				if (product.type == 1) {
					if (product.goodsId.selected_model) {
						let num = 0;
						for (let model of product.goodsId.selected_model) {
							for (let item of res.models) {
								if (item.id == model.id) {
									item.reserve = Number(item.reserve) - Number(model.num)
									//console.log(item.reserve)
									num += Number(model.num)
								}
							}
						}
						//console.log(res.models)
						query1.set('models', res.models)
						query1.set('reserve', res.reserve - num);
					}else{
						query1.set('reserve', res.reserve - product.num);
					}
				} else if (product.type == -1) {
					if (product.goodsId.selected_model) {
						let num = 0;
						for (let model of product.goodsId.selected_model) {
							for (let item of res.models) {
								if (item.id == model.id) {
									item.reserve = Number(item.reserve) + Number(model.num)
									//console.log(item.reserve)
									num += Number(model.num)
								}
							}
						}
						//console.log(res.models)
						query1.set('models', res.models)
						query1.set('reserve', res.reserve + num);
					}else{
						query1.set('reserve', res.reserve + product.num);
					}
					
				}
				query1.save().then(res => {
					if (thisGood.header) {
						const query1 = Bmob.Query("Goods");
						query1.equalTo("header", "==", thisGood.header.objectId);
						query1.equalTo("order", "==", 1);
						query1.statTo("sum", "reserve");
						query1.find().then(res => {
							console.log("当前的产品的主产品", res)
							let now_reserve = res[0]._sumReserve
							const query = Bmob.Query('Goods');
							query.set('reserve', now_reserve)
							query.set('stocktype', (now_reserve > thisGood.warning_num) ? 1 : 0)
							query.set('id', thisGood.header.objectId)
							query.save().then(res => {
								if (i == (that.products.length - 1)) {
									uni.hideLoading();
									uni.navigateBack({
										delta: 1
									})
									setTimeout(function() {
										uni.showToast({
											title: '撤销成功'
										})
									}, 1000);
								}
							})
						})
					}else{
						if (i == (that.products.length - 1)) {
							uni.hideLoading();
							uni.navigateBack({
								delta: 1
							})
							setTimeout(function() {
								uni.showToast({
									title: '撤销成功'
								})
							}, 1000);
						}
					}
					
					
				})

			}).catch(err => {
				console.log(err)
			})

		}

	}
}
